import { useState } from "react";
import { Container, Row, Col, Modal } from "react-bootstrap";


import Imj1 from "../assets/img/twid plat/TW001.JPG";
import Imj2 from "../assets/img/twid plat/TW002.JPG";
import Imj3 from "../assets/img/twid plat/TW003.JPG";
import Imj4 from "../assets/img/twid plat/TW004.JPG";
import Imj5 from "../assets/img/twid plat/TW005.JPG";
import Imj6 from "../assets/img/twid plat/TW006.JPG";

import "animate.css";
import "../App.css";

import { Footer } from "../components/Footer";
import TrackVisibility from "react-on-screen";

const images = [
  { src: Imj1, text: "Dcouvrez la fouta Twid Plat de Du Artex Tunisie...", sku: "TW001" },
  { src: Imj2, text: "Dcouvrez la fouta Twid Plat de Du Artex Tunisie...", sku: "TW002" },
  { src: Imj3, text: "Dcouvrez la fouta Twid Plat de Du Artex Tunisie...", sku: "TW003" },
  { src: Imj4, text: "Dcouvrez la fouta Twid Plat de Du Artex Tunisie...", sku: "TW004" },
  { src: Imj5, text: "Dcouvrez la fouta Twid Plat de Du Artex Tunisie...", sku: "TW005" },
  { src: Imj6, text: "Dcouvrez la fouta Twid Plat de Du Artex Tunisie...", sku: "TW006" },
];

const TwidPlatD = () => {
  const [showModal, setShowModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const [activeIndex, setActiveIndex] = useState(0);

  const handleShow = (index) => {
    setSelectedImage(images[index]); // now storing the full object
    setActiveIndex(index);
    setShowModal(true);
  };

  const handleClose = () => setShowModal(false);

  return (
    <>
      <section className="banner1" id="home">
        <Container>
          <Row className="align-items-center">
            {/* Text Content */}
            <Col xs={12} md={6} xl={4}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                    <span className="tagline">ARTEX TUNISIE</span>
                    <h1>TWID PLAT</h1>
                    <p>{images[activeIndex].text}</p>
                    <p><strong>SKU:</strong> {images[activeIndex].sku}</p>
                  </div>
                )}
              </TrackVisibility>
            </Col>

            {/* Images Grid */}
            <Col xs={12} md={6} xl={8}>
              <TrackVisibility>
                {({ isVisible }) => (
                  <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                    <Row>
                      {images.map((item, index) => (
                        <Col xs={6} md={4} className="mb-3" key={index}>
                          <img
                            src={item.src}
                            alt={`Twid Plat ${index + 1}`}
                            className="img-fluid image-grid"
                            onClick={() => handleShow(index)}
                          />
                        </Col>
                      ))}
                    </Row>
                  </div>
                )}
              </TrackVisibility>
            </Col>
          </Row>
        </Container>
      </section>

      <Footer />

      {/* Modal for Zoom Image */}
      <Modal show={showModal} onHide={handleClose} centered>
        <Modal.Body className="text-center">
          {selectedImage && (
            <>
              <img
                src={selectedImage.src}
                alt="Zoomed"
                className="img-fluid zoom-image mb-3"
              />
              <div>
                <p><strong>SKU:</strong> {selectedImage.sku}</p>
              </div>
            </>
          )}
        </Modal.Body>
      </Modal>
    </>
  );
};

export default TwidPlatD;
